.footer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: rgb(35, 35, 40);
  color: white;
  padding: 2.5vw;
  min-height: 100vh;
}

.footerList {
  list-style: none;
  margin: 7.5vw 0 0 0;
  padding: 0;
  height: 40%;
  justify-content: space-between;
}

.footerListItem {
  margin: 0 0 auto 0;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  height: 3.5vw;
}

.footerLink {
  font-family: "Barlow Condensed", sans-serif;
  color: white;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  text-decoration: none;
}

.footerCopywrite {
  display: flex;
  height: 100%;
  width: 50%;
  align-items: flex-end;
  margin: 7.5vw 0 5vw 0;
}

@media only screen and (orientation: portrait) {
  .footerLink {
  }
  .footerCopywrite {
    width: fit-content;
    margin: 10.5vh 5vh 5.5vh 5vh;
  }
  .footer {
    padding: 2.5vh;
    height: calc(100vh - 5vh);
  }
  .footerList {
    margin: 0 5vh;
  }
  .footerListItem {
    margin: 0 0 4vh 0;
  }
}

@media only screen and (orientation: portrait) and (width < 800px),
  (width < 800px) {
  .footerLink {
  }
  .footerList {
    margin: 2.5vh 0;
  }
  .footerCopywrite {
    width: fit-content;
    margin: 10.5vh 0 5.5vh 0;
  }
}
